html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  margin: 0px;
}
.main-container {
  max-width: 59rem;
  margin: 0 auto;
  position: relative;
  padding-top: 5rem;
}
.feed-header {
  position: fixed;
  background-color: white;
  height: 3.25rem;
  margin: 0 auto;
  width: 100%;
  z-index: 1;
  top: 0;
  border-bottom: 1px solid #c9c9c9;
}
.feed-header-wrapper {
  max-width: 59rem;
  margin: 0 auto;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.feed-header input {
  border: 1px solid #c9c9c9;
  border-radius: 3px;
  background-color: #fafafa;
  font-size: 0.875rem;
  line-height: 1.4rem;
  text-align: center;
  width: 13.5rem;
}
.feed-header input:focus {
  outline: none;
}
.header-right {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.header-right-nav a {
  margin: 0 0.5rem;
}

.profile-nav-container {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 0.5rem;
}

.profile-nav-container img {
  width: 100%;
}

.sidebar-wrapper {
  width: 19rem;
  position: absolute;
  top: 3.25rem;
  right: 0;
}

.sidebar {
  position: fixed;
}
.user-info {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 1rem;
}
.user-image {
  height: 58px;
  width: 58px;
  border-radius: 50%;
  overflow: hidden;
}
.user-image img {
  width: 100%;
}
.user-name {
  margin-left: 1rem;
}
.user-name p,
.follow-name p {
  font-size: 0.75rem;
  line-height: 1.125rem;
  font-weight: 400;
  color: #8e8e8e;
  margin: 0;
}
.user-name p a,
.follow-name p a {
  font-size: 0.875rem;
  line-height: 1.125rem;
  font-weight: 600;
  text-decoration: none;
  color: #262626;
}
.follow-name p a:hover {
  text-decoration: underline;
}
table {
  width: 19rem;
}
.follow-info {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}
.follow-image {
  height: 32px;
  width: 32px;
  border-radius: 50%;
  overflow: hidden;
}
.follow-image img {
  width: 100%;
}
.follow-name {
  margin-left: 1rem;
}
td,
th {
  padding: 0.5rem 0;
}
td button {
  border: none;
  background-color: inherit;
  cursor: pointer;
  font-size: 0.75rem;
  color: #0095f6;
  font-weight: 600;
  padding: 0;
}
td button:focus {
  outline: none;
}
th {
  font-size: 0.875rem;
  font-weight: 600;
  color: #8e8e8e;
}
th a {
  font-size: 0.75rem;
  font-weight: 600;
  color: #262626;
  text-decoration: none;
}
.cell-left {
  text-align: left;
}
.cell-right {
  text-align: right;
}
.sidebar-footer p a,
.sidebar-footer p {
  font-size: 0.6875rem;
  line-height: 1rem;
  color: #c7c7c7;
  text-decoration: none;
}
.sidebar-footer p {
  margin: 0;
}
.copyright {
  padding-top: 1rem;
}

/* css for individual posts */
.post {
  font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
  background-color: white;
  margin-bottom: 4rem;
  padding: 0;
}

.post p,
.post li {
  font-size: 0.875rem;
  line-height: 1.125rem;
  color: #262626;
  font-weight: 400;
}

.post p a,
.post li a {
  font-size: 0.875rem;
  line-height: 1.125rem;
  color: #262626;
  font-weight: 600;
  text-decoration: none;
}
.post p a:hover,
.post li a:hover {
  text-decoration: underline;
}
.post {
  width: 38.6rem;
  border: 1px solid #c9c9c9;
}
.post button {
  border: none;
  background-color: white;
  cursor: pointer;
}

.post button:focus {
  outline: none;
}

.post header p {
  display: inline;
}
.post header {
  height: 3.75rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #c9c9c9;
  background-color: white;
}
.left-header {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-content: center;
}
.post-icon {
  width: 1.8rem;
  height: 1.8rem;
  align-self: center;
  margin: 0 1rem;
}
.post-icon img {
  width: 100%;
  border-radius: 50%;
}
.post-image {
  position: relative;
}
.post-image img {
  width: 100%;
}
.like-container {
  position: absolute;
  top: 15.5rem;
  left: 15.5rem;
  opacity: 50%;
  filter: drop-shadow(0 0 0.5rem #1b1b1b);
}
.footer-top-container {
  padding: 0.5rem;
}
.icon-bar-items {
  height: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.comment {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.post-age a {
  font-size: 0.625rem;
  line-height: 1.125rem;
  color: #8e8e8e;
  font-weight: 400;
  text-decoration: none;
}
.comment-input {
  height: 3.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #c9c9c9;
}
.comment li {
  list-style-type: none;
}
.comment ul {
  margin: 0;
  padding: 0;
}
.comment-input textarea {
  width: 85%;
  margin: 2%;
  font-family: inherit;
  font-size: 0.875rem;
  line-height: 1.125rem;
  color: #262626;
  font-weight: 400;
  border: none;
  height: 100%;
  resize: none;
  outline: none;
  padding-top: 1rem;
}
.comment-input button {
  width: 8%;
  margin: 2%;
  color: #0095f6;
  font-weight: 600;
  font-size: 0.875;
}
.comment-input button:disabled {
  opacity: 50%;
  cursor: unset;
}

/* css for login page */
.login-main {
  height: 92vh;
  display: flex;
  flex-flow: row nowrap;
  width: 49rem;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}
.slideshow {
  width: 24rem;
  height: 40rem;

  background-image: url(./images/phone.png);
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}
.slideshow img {
  position: absolute;
  top: 6.8rem;
  left: 7.2rem;
}
.login {
  width: 22rem;
  border: 1px #c9c9c9;
}
.signup {
  width: 100%;
  height: 4rem;
  background-color: white;
  text-align: center;
  border: 1px solid #c9c9c9;
  padding-top: 0.4375rem;
  margin-top: 0.7rem;
}
.signup p {
  font-size: 0.875rem;
  font-weight: 300;
  color: #262626;
}
.signup a {
  font-size: 0.875rem;
  font-weight: 600;
  color: #0095f6;
  text-decoration: none;
}
.get-app {
  text-align: center;
}
.get-app p {
  color: #262626;
  font-size: 0.875rem;
}
.app-links {
  width: 100%;
  display: flex;
  flex-wrap: row nowrap;
  justify-content: center;
}
.app-links img {
  width: 8.5rem;
  margin: 0.2rem;
}
.login-form-wrapper {
  width: 100%;
  background-color: white;
  text-align: center;
  border: 1px solid #c9c9c9;
  padding: 2rem 2.5rem 1rem 2.5rem;
}
.login-form-header img {
  width: 11rem;
}
.login-form input {
  width: 100%;
  border-radius: 3px;
  border: 1px solid #c9c9c9;
  margin: 0.2rem;
  background-color: #fafafa;
  height: 2.2rem;
  padding: 0 0.5rem;
}
.login-form input:focus {
  outline: none;
}
.login-form button {
  background-color: #0095f6;
  color: white;
  border-radius: 5px;
  width: 100%;
  border: none;
  height: 1.8rem;
  font-size: 0.875rem;
  font-weight: 600;
  font-family: inherit;
  outline: none;
  margin: 0.4rem;
}
.login-form button:disabled {
  opacity: 30%;
  cursor: unset;
}
.divider {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.divider p {
  color: #8e8e8e;
  font-weight: 600;
  font-size: 0.8125rem;
}
.password-hint p {
  margin: 0;
  color: red;
  font-size: 0.875rem;
}
.leftline,
.rightline {
  border-top: 1px solid #c9c9c9;
  width: 40%;
  height: 0;
}
.facebook a {
  color: #385185;
  font-weight: 600;
  font-size: 0.875rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.8rem;
}
.forgot-password a {
  font-size: 0.75rem;
  color: #00376b;
  text-decoration: none;
  margin: 0.25rem;
}

.login-footer {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 72rem;
  width: 100vw;
  height: 5vh;
  margin: 0 auto;
}
.login-footer-left {
  display: flex;
  flex-flow: row wrap;
  justify-content: left;
  align-items: flex-start;
}
.login-footer-left a {
  font-size: 0.75rem;
  color: #00376b;
  font-weight: 600;
  text-decoration: none;
  margin: 0.5rem;
}
.login-footer-right p {
  font-size: 0.75rem;
  color: #8e8e8e;
  font-weight: 600;
  margin: 0.2rem;
}

/* window 980px wide and less) */
@media (max-width: 980px) {
  .sidebar {
    display: none;
  }
  .post {
    margin: 0 auto;
  }
}

/* window 860px wide and less) */
@media (max-width: 860px) {
  .login-main {
    justify-content: center;
  }
  .slideshow {
    display: none;
  }
  .login-footer {
    display: unset;
    text-align: center;
  }
  .login-footer-left {
    display: unset;
  }
}
